<template>
  <div class="page-header clear-filter" filter-color="orange">
    <modal :show.sync="modals.modal2" headerClasses="justify-content-center">
            <h4 slot="header" class="title title-up">Ingresa tu correo</h4>
            <fg-input class="no-border form-control-lg"
                    placeholder="Correo..."
                    v-model="mailRescue"
                    addon-left-icon="now-ui-icons ui-1_email-85">
            </fg-input>
            <template slot="footer">
            <n-button type="danger" @click.native="modals.modal2 = false">Cerrar</n-button>    
            <n-button type="success" v-on:click="rescuePassword()">Recuperar</n-button>
            </template>
        </modal>
        <modal :show.sync="modals.modal3"
          class="modal-primary"
          footer-classes="justify-content-center"
          >
          <card class="w-100" style="max-width:100%" type="login" plain>
              <div class="w-25 mx-auto mb-4">
                  <img class="w-100" src="img/logo.png" alt="">
              </div>
              <div class="row">
                  <fg-input
                      class="no-border input-lg col-12" id="typePassRescueOne"
                      addon-left-icon="now-ui-icons ui-1_lock-circle-open"
                      placeholder="Nueva contrasea"
                      type="password"
                      v-model="change.password.value"
                      v-on:keyup="verifyRegisterChange"
                      
                      :value="change.password.validValue" 
                      >
                  </fg-input>
                  
                  <!-- <div class="col-2">
                      <n-button type="primary" icon round style="margin-top:3px;" v-on:click="typePassRescue()">
                          <i class="fa fa-eye eyesPassword text-white w-100" v-if="typePass == 'pass'"></i>
                          <i class="fa fa-eye-slash eyesPassword text-white w-100" v-else></i>
                      </n-button>
                  </div> -->
                  <fg-input
                      class="no-border input-lg col-12" id="typePassRescueTwo"
                      addon-left-icon="now-ui-icons ui-1_lock-circle-open"
                      placeholder="Repita su contrasea"
                      type="password"
                      v-model="change.passwordRep.value"
                      v-on:keyup="verifyRegisterChange"
                      
                      :value="change.passwordRep.validValue" 
                      >
                  </fg-input>
                  <!-- <div class="col-2">
                      <n-button type="primary" icon round style="margin-top:3px;" v-on:click="typePassRescue()">
                          <i class="fa fa-eye eyesPassword text-white w-100" v-if="typePass == 'pass'"></i>
                          <i class="fa fa-eye-slash eyesPassword text-white w-100" v-else></i>
                      </n-button>
                  </div> -->
              </div>
              <div class="row">
                  <div class="text-center col-md-6">
                      <button
                      style="color:black"
                      class="btn btn-info btn-round btn-block"
                      v-on:click="rescuePass()"
                      >Cambiar contrasea</button
                      >
                  </div>
              </div>
          </card>
        </modal>
    <div
      class="page-header-image"
      style="background-image: url('img/login.jpg')"
    ></div>
    <div class="content">
      <div class="container">
        <div class="col-md-5 ml-auto mr-auto">
          <card v-if="register == false" type="login" plain>
            <div slot="header" class="logo-container">
              <img v-lazy="'img/logo.png'" alt="" />
            </div>

            <fg-input
              class="no-border input-lg"
              addon-left-icon="now-ui-icons users_circle-08"
              placeholder="Correo"
              v-model="email"
            >
            </fg-input>

            <fg-input
              class="no-border input-lg"
              addon-left-icon="now-ui-icons objects_key-25"
              placeholder="Contrasea"
              type="password"
              v-model="pass"
            >
            </fg-input>

            <template slot="raw-content">
              <div class="card-footer text-center">
                <a
                  @click="login()"
                  style="cursor:pointer"
                  class="btn btn-primary btn-round btn-lg btn-block"
                  >Ingresar</a
                >
              </div>
              <div class="pull-left">
                <h6>
                  <a @click="register = true" style="cursor:pointer" class="link footer-link backLetters">Crear cuenta</a>
                </h6>
              </div>
              <div class="pull-right">
                <h6>
                  <a v-on:click="modals.modal2 = true" style="cursor:pointer" class="link footer-link mt-1 backLetters">Olvidaste tu clave?</a>
                </h6>
              </div>
            </template>
          </card>
          <card class="registerForm" v-else type="login" plain>
            <div slot="header" class="logo-container">
              <img v-lazy="'img/logo.png'" alt="" />
            </div>

            <fg-input
              class="no-border input-lg"
              addon-left-icon="now-ui-icons users_circle-08"
              placeholder="Nombre"
              v-model="dataRegister.name"
            >
            </fg-input>

            <fg-input
              class="no-border input-lg"
              addon-left-icon="now-ui-icons users_circle-08"
              placeholder="Apellido"
              v-model="dataRegister.lastName"
            >
            </fg-input>

            <fg-input
              class="no-border input-lg"
              addon-left-icon="now-ui-icons users_circle-08"
              placeholder="Correo"
              v-model="dataRegister.email"
            >
            </fg-input>

            <!-- <fg-input
              class="no-border input-lg"
              addon-left-icon="now-ui-icons tech_mobile"
              placeholder="Nmero de telfono"
              type="text"
              v-model="dataRegister.phone"
            > -->
            <!-- </fg-input> -->
            <div class="row">
              <div class="col-md-2 col-3 p-0 pt-1 pb-1">
                  <fg-input
                      class="no-border input-lg p-0 w-100"
                      v-model="dataRegister.code"
                      readonly
                      >
                  </fg-input>
              </div>
              <div class="col-md-10 col-9 p-1">
                  <fg-input
                      class="no-border input-lg w-100 pr-0"
                      addon-left-icon="now-ui-icons tech_mobile"
                      placeholder="Telfono"
                      type="text"
                      v-on:input="changeFormat()"
                      maxlength="9"
                      v-model="dataRegister.phone"
                      >
                  </fg-input>
              </div>
            </div>
             
            <div class="row">
              <fg-input
                class="no-border input-lg col-10"
                addon-left-icon="now-ui-icons objects_key-25"
                placeholder="Contrasea"
                :type="passType"
                v-model="dataRegister.pass"
              >
              
              </fg-input>
              <div class="col-2">
                  <n-button type="primary" icon round style="margin-top:3px;" v-on:click="passType == 'password' ? passType = 'text': passType = 'password'">
                      <i class="fa fa-eye eyesPassword text-white w-100" v-if="typePass == 'pass'"></i>
                      <i class="fa fa-eye-slash eyesPassword text-white w-100" v-else></i>
                  </n-button>
              </div>
              <fg-input
                class="no-border input-lg col-10"
                addon-left-icon="now-ui-icons objects_key-25"
                placeholder="Reepetir contrasea"
                :type="passTypeR"
                v-model="dataRegister.passR"
              >
              </fg-input>
              <div class="col-2">
                  <n-button type="primary" icon round style="margin-top:3px;" v-on:click="passTypeR == 'password' ? passTypeR = 'text': passTypeR = 'password'">
                      <i class="fa fa-eye eyesPassword text-white w-100" v-if="typePass == 'pass'"></i>
                      <i class="fa fa-eye-slash eyesPassword text-white w-100" v-else></i>
                  </n-button>
              </div>
            </div>
            

            <template slot="raw-content">
              <div class="card-footer text-center">
                <a
                  @click="registerClient()"
                  style="cursor:pointer"
                  class="btn btn-primary btn-round btn-lg btn-block"
                  >Registrar</a
                >
              </div>
              <div class="pull-left">
                <h6>
                  <a @click="register = false" style="cursor:pointer" class="link footer-link mt-1 backLetters">Ya tienes cuenta?</a>
                </h6>
              </div>
            </template>
          </card>
        </div>
      </div>
    </div>
    
  </div>
</template>
<script>
import { Card, Button, FormGroupInput,Modal } from '@/components';
import MainFooter from '@/layout/MainFooter';
import axios from 'axios'
import router from '../router'
import endPoint from '../../config-endpoint/endpoint.js'
import jwtDecode from 'jwt-decode'
import EventBus from "./components/eventBus"


export default {
  name: 'login-page',
  bodyClass: 'login-page',
  components: {
    Card,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput,
    Modal
  },
  data(){
    return {
      modals:{
        modal2:false,
        modal3:false
      },
      mailRescue:'',
      typePass: false,
      change: {
        password: {
          value: '',
          validClass: 'has-danger',
          validValue: 'Error'
        },
        passwordRep: {
          value: '',
          validClass: 'has-danger',
          validValue: 'Error'
        }
      },
      rescue:this.$route.query.code,
      email:'',
      pass:'',
      configHeader: {headers:{"x-database-connect":endPoint.dataBase}},
      dataRegister:{
        name:'',
        lastName:'',
        email:'',
        pass:'',
        passR:'',
        phone:'',
        code:'+57'
      },
      passTypeR:'password',
      passType:'password',
      register:false
    }
  },
  created () {
      this.getRescue()
  },
  methods: {
    getRescue(){
      if (this.rescue) {
        const split = this.rescue.split('?')
        const date = new Date()
        const rest = parseFloat(date.getTime()) - parseFloat(split[0])
        if (rest > 630000) {
          this.$swal({
              type: 'error',
              icon: 'error',
              timerProgressBar: true,
              title: 'Su link de recuperacion expiro',
              showConfirmButton: true,
              didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
              }
          })
        }else{
          this.modals.modal3 = true
        }
      }
    },
    rescuePassword(){
        axios.post(endPoint.endpointTarget+'/mails/rescuePass',{
          mail:this.mailRescue
        },this.configHeader)
        .then(res => {
            if (res.data.status == 'ok') {
              
                this.$swal({
                    type: 'success',
                    icon: 'success',
                    
                    timer: 3000,
                    timerProgressBar: true,
                    title: 'Verifica tu correo para recuperar tu contrasea',
                    showConfirmButton: false,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', Swal.stopTimer)
                        toast.addEventListener('mouseleave', Swal.resumeTimer)
                    }
                })

            }else{
                this.$swal({
            type: 'error',
            icon: 'error',
            timer: 3000,
            timerProgressBar: true,
            title: 'Correo no registrado',
            showConfirmButton: false,
            didOpen: (toast) => {
                toast.addEventListener('mouseenter', Swal.stopTimer)
                toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
        })
            }
        })
        .catch(err =>{
            console.log(err)
        })
    },
    rescuePass(){
      if (this.change.password.validValue == 'Error') {
        this.$swal({
            type: 'error',
            icon: 'error',
            timer: 3000,
            timerProgressBar: true,
            title: 'Minimo 8 caracteres',
            showConfirmButton: false,
            didOpen: (toast) => {
                toast.addEventListener('mouseenter', Swal.stopTimer)
                toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
        })
      }else if(this.change.passwordRep.validValue == 'Error'){
        this.$swal({
            type: 'error',
            icon: 'error',
            timer: 3000,
            timerProgressBar: true,
            title: 'Las contraseas no coinciden',
            showConfirmButton: false,
            didOpen: (toast) => {
                toast.addEventListener('mouseenter', Swal.stopTimer)
                toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
        })
      }else{
        axios.post(endPoint.endpointTarget+'/clients/rescueChange', {
          password: this.change.password.value,
          code: this.rescue
        },this.configHeader)
        .then(res => {
          if (res.data.status == 'ok') {
            this.$swal({
                    type: 'success',
                    icon: 'success',
                    
                    timer: 3000,
                    timerProgressBar: true,
                    title: 'Contrasea actualizada correctamente',
                    showConfirmButton: false,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', Swal.stopTimer)
                        toast.addEventListener('mouseleave', Swal.resumeTimer)
                    }
                })
                this.modals.modal3 = false
          }else{
            this.$swal({
            type: 'error',
            icon: 'error',
            timer: 3000,
            timerProgressBar: true,
            title: 'Errores tecnicos',
            showConfirmButton: false,
            didOpen: (toast) => {
                toast.addEventListener('mouseenter', Swal.stopTimer)
                toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
        })
          }
          console.log(res)
        })
      }
    },
    verifyRegisterChange(){
      this.change.password.validClass = this.change.password.value.length > 7 ? 'has-success' : 'has-danger'
      this.change.password.validValue = this.change.password.value.length > 7 ? 'Success' : 'Error'
      this.change.passwordRep.validClass = this.change.passwordRep.value == this.change.password.value ? 'has-success' : 'has-danger'
      this.change.passwordRep.validValue = this.change.passwordRep.value == this.change.password.value ? 'Success' : 'Error'
    },
    registerClient() {
      if (this.dataRegister.name == '' || this.dataRegister.lastName == '' || this.dataRegister.email == '' || this.dataRegister.pass == '' || this.dataRegister.phone == '') {
        
        this.$swal({
                    type: 'error',
                    icon: 'error',
                    timer: 3000,
                    timerProgressBar: true,
                    title: 'Debes rellenar todos los datos',
                    showConfirmButton: false,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', Swal.stopTimer)
                        toast.addEventListener('mouseleave', Swal.resumeTimer)
                    }
                })
      }
      else{
          var valid = false
        if (this.dataRegister.email.includes("@")) {
          if (this.dataRegister.email.split("@")[1].includes(".")) {
            valid = true
          }
          
        }
        else{
          this.$swal({
              type: 'error',
              icon: 'error',
              timer: 3000,
              timerProgressBar: true,
              title: 'Debes ingresar un correo valido',
              showConfirmButton: false,
              didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
              }
          })
        }
        if (valid && this.dataRegister.pass.length < 8) {
          valid = false
          this.$swal({
              type: 'error',
              icon: 'error',
              timer: 3000,
              timerProgressBar: true,
              title: 'La contrasea debe contener al menos 8 caracteres',
              showConfirmButton: false,
              didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
              }
          })
        }
        if (valid && this.dataRegister.phone.length < 9) {
          valid = false
          this.$swal({
              type: 'error',
              icon: 'error',
              timer: 3000,
              timerProgressBar: true,
              title: 'Introduzca un nmero de telefono valido',
              showConfirmButton: false,
              didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
              }
          })
        }
        if(valid && this.dataRegister.pass != this.dataRegister.passR){
          valid = false
          this.$swal({
              type: 'error',
              icon: 'error',
              timer: 3000,
              timerProgressBar: true,
              title: 'Las contraseas no coinciden',
              showConfirmButton: false,
              didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
              }
          })
        }
        if (this.dataRegister.name != '' && this.dataRegister.lastName != '' && this.dataRegister.email != '' && this.dataRegister.pass != '' && this.dataRegister.phone != '' && valid) {
            axios.post(endPoint.endpointTarget+'/clients/register',{
              name:this.MaysPrimera(this.dataRegister.name.toLowerCase()),
              lastName:this.MaysPrimera(this.dataRegister.lastName.toLowerCase()),
              mail:this.dataRegister.email.toLowerCase(),
              pass:this.dataRegister.pass,
              phone:this.dataRegister.phone
          },this.configHeader)
          .then(res => {

              if (res.data.status == 'client create') {
                  this.$swal({
                      type: 'success',
                      icon: 'success',
                      toast: true,
                      position: 'top-end',
                      timer: 3000,
                      timerProgressBar: true,
                      title: 'Registro exitoso, ya puedes ingresar',
                      showConfirmButton: false,
                      didOpen: (toast) => {
                          toast.addEventListener('mouseenter', Swal.stopTimer)
                          toast.addEventListener('mouseleave', Swal.resumeTimer)
                      }
                  })
                  this.register = false
                  this.dataRegister.name = ''
                  this.dataRegister.lastName = ''
                  this.dataRegister.email = ''
                  this.dataRegister.pass = ''
                  this.dataRegister.phone = ''
                  this.dataRegister.passR = ''
                  console.log(res.data.id)
                  axios.get(endPoint.endpointTarget+'/mails/mailRegister/'+res.data.id)
              }
              if (res.data.status == 'client exist') {
                  this.$swal({
                      type: 'error',
                      icon: 'error',
                      timer: 3000,
                      timerProgressBar: true,
                      title: 'Este correo ya se encuentra registrado',
                      showConfirmButton: false,
                      didOpen: (toast) => {
                          toast.addEventListener('mouseenter', Swal.stopTimer)
                          toast.addEventListener('mouseleave', Swal.resumeTimer)
                      }
                  })
              }
              
              
          })
        }
      }
      
      
    },
    MaysPrimera(string){
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    changeFormat(){
        var number = this.dataRegister.phone.replace(/[^\d]/g, '')
        if (number.length == 9) {
            number = number.replace(/(\d{1})(\d{4})/, "$1-$2-");
        } else if (number.length == 10) {
            number = number.replace(/(\d{3})(\d{3})(\d{4})/, "($1) $2-$3");
        }
        this.dataRegister.phone = number
    },
    login(){
      if (this.pass != '' && this.email != '') {
          axios.post(endPoint.endpointTarget+'/clients/login',{
            mail:this.email.toLowerCase(),
            pass:this.pass
          },this.configHeader)
          .then(res => {

              if (res.data.status == 'client login') {
                  this.$swal({
                      type: 'success',
                      icon: 'success',
                      toast: true,
                      position: 'top-end',
                      timer: 3000,
                      timerProgressBar: true,
                      title: 'Ingreso exitoso',
                      showConfirmButton: false,
                      didOpen: (toast) => {
                          toast.addEventListener('mouseenter', Swal.stopTimer)
                          toast.addEventListener('mouseleave', Swal.resumeTimer)
                      }
                  })
                  localStorage.setItem("status","log-in")
                  localStorage.setItem("clientMail",this.email.toLowerCase())
                  localStorage.setItem("clientName",res.data.name)
                  localStorage.setItem("clientlastName",res.data.lastName)
                  localStorage.setItem("clientId",res.data.id)
                  EventBus.$emit("addProduct", "products")
                  router.push("/")
              }
              if (res.data.status == 'client incorrect') {
                  this.$swal({
                      type: 'error',
                      icon: 'error',
                      timer: 3000,
                      timerProgressBar: true,
                      title: 'Este correo no se encuentra registrado',
                      showConfirmButton: false,
                      didOpen: (toast) => {
                          toast.addEventListener('mouseenter', Swal.stopTimer)
                          toast.addEventListener('mouseleave', Swal.resumeTimer)
                      }
                  })
              }
              if (res.data.status == 'client blocked') {
                  this.$swal({
                      type: 'error',
                      icon: 'error',
                      timer: 3000,
                      timerProgressBar: true,
                      title: 'Usuario bloqueado',
                      showConfirmButton: false,
                      didOpen: (toast) => {
                          toast.addEventListener('mouseenter', Swal.stopTimer)
                          toast.addEventListener('mouseleave', Swal.resumeTimer)
                      }
                  })
              }
              if (res.data.status == 'pass incorrect') {
                  this.$swal({
                      type: 'error',
                      icon: 'error',
                      timer: 3000,
                      timerProgressBar: true,
                      title: 'Contrasea incorrecta',
                      showConfirmButton: false,
                      didOpen: (toast) => {
                          toast.addEventListener('mouseenter', Swal.stopTimer)
                          toast.addEventListener('mouseleave', Swal.resumeTimer)
                      }
                  })
              }
              
          })
        }
        else{
          this.$swal({
              type: 'error',
              icon: 'error',
              timer: 3000,
              timerProgressBar: true,
              title: 'Debe rellenar todos los campos',
              showConfirmButton: false,
              didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
              }
          })
        }
    }
  }
};
</script>
<style>

  .backLetters{
    padding: 6px;
    border-radius: 10px;
    font-size: 1.3em !important;
  }

</style>
